<template>
  <gmap-map
    style="width: 100%; height: 100%; min-height: 600px"
    :center="gCenter"
    :zoom="zoom"
    :settings="settings"
  >
    <slot></slot>
  </gmap-map>
</template>

<script>
import MixinMap from '../../mixins/Map.js';

import Vue from 'vue';
import * as VueGoogleMaps from 'vue2-google-maps';

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyCpSKazmH7T7ArBkdcPiRYaYG_QKhj7rRM',
    libraries: 'geometry',
  },
});

export default {
  name: 'google-map',
  mixins: [MixinMap],

  props: {
    settings: {
      required: false,
      default: () => ({}),
    },
  },

  computed: {
    gCenter() {
      return this.center
        ? {
            lng: this.center[0],
            lat: this.center[1],
          }
        : {
            lng: 0,
            lat: 0,
          };
    },
  },
};
</script>